<script>
export default {
	name: "App",
	components: {},
	data() {
		return {
			currentTheme: "",
		};
	},
	methods: {
		toggleTheme() {
			this.currentTheme = this.currentTheme == "dark" ? "" : "dark";
			document.documentElement.setAttribute(
				"data-theme",
				this.currentTheme
			);
			localStorage.setItem("currentTheme", this.currentTheme);
		},
	},
};
</script>

